import React, {Component} from 'react';
import styled from 'styled-components';

//*Import Components
import Header from "../components/Header";
import Footer from "../components/Footer";
import CardList from "../components/Cards/CardList";

//*Import image
import cardBack from "../assets/playing-card-back.png";
//*API URLS to remember
const API_NEW_DECK = "https://deckofcardsapi.com/api/deck/new/";
const API_SHUFFLE = "https://deckofcardsapi.com/api/deck/<<deck_id>>/shuffle/";
const API_BASE_URL = "https://deckofcardsapi.com/api/deck/";
/*
 TODO: Main
 TODO: Before component Mounts, request new deck, set state
 TODO: CardList - Use DeckID from state, draw Cards from cardlist
*/
class Main extends Component
{

    /*
  TODO: State
*/
    state = {

        deck: {},
        cards: [],
        cardOne: {},
        cardTwo: {},
        indexOne: 0,
        indexTwo: 0,
        selected: 0,
        match: false,
        cardPile: []

    }

    //TODO: Lifecycle methods:
    componentWillMount()
    {
        //*Fetch new deck from API before component mounts
        fetch(API_NEW_DECK)
            .then(res => res.json())
            .then(deck => {
                this.setState({
                    ...this.state,
                    deck

                });
                console.log("API - New Deck", this.state.deck);
                return deck.deck_id;
            })
            .then(deckID => {
                // ! Nest Fetch, Draw 52 cards from deck after retrieving initial deck *Draw 52
                this._drawCards(deckID);
            })
            .catch(e => console.log(e));
        //*Debug

    }

    //! Custom Functions

    /*
 TODO: API Card Functions
 */
    _drawCards = (deckID) => {
        fetch(`${API_BASE_URL}${deckID}/draw/?count=52`)
            .then(res => res.json())
            .then(data => {
                this.setState({
                    ...this.state,
                    cards: data.cards

                });

            })
            .catch(e => console.log(e));
    }

    //! Shuffle Cards
    /*
    TODO: shuffleDeck()
    */
    _shuffleDeck = () => {
        const deckID = this.state.deck.deck_id;
        fetch(`https://deckofcardsapi.com/api/deck/${deckID}/shuffle/`)
            .then(res => res.json())
            .then(deck => {
                // *Update deck, deckId does not change *cardPile is reset to be an empty array
                // as well
                this.setState({
                    ...this.state,
                    deck,
                    cardPile: []

                })
                return deck.deck_id;
            })
            .then(deckID => {
                this._drawCards(deckID);
            })
            .catch(e => console.log(e));
    }
    //! - Match Card Functions
    /*
    TODO: selectCard()
    */
    _selectCard = () => {
        //! Increment state.selected if less than 2 Only two cards can be selected.
        if (this.state.selected > 2) {
            this.setState({
                ...this.state,
                selected: this.state.selected++
            });
        }
        if (this.state.selected === 2) {
            //*Run match function and reset selected
            this.setState({
                ...this.state,
                selected: 0
            });
        }
    }
    _setCards = (e) => {

        //*Pull variables
        const card = e.currentTarget.dataset.value;
        const index = e.currentTarget.dataset.index;
        const image = e.currentTarget.dataset.image;
        const {selected, indexOne} = this.state;
        //*Grab all cards, find index and change src
        const domCards = document.querySelectorAll('.cardlist__card--img');

        //*If selected is 0, update CardOne
        if (selected === 0) {
            this.setState({
                ...this.state,
                cardOne: card,
                selected: this.state.selected + 1,
                indexOne: Number(index)
            })
            //*If selected is 1, update CardTwo ! If Image, update image
            if (domCards[index].src !== null) {
                domCards[index].src = image;
            }
        } else if (selected === 1 && indexOne != index) {
            //*If chosing second card, indexOne cannot match current index;
            this.setState({
                ...this.state,
                cardTwo: card,
                selected: this.state.selected + 1,
                indexTwo: Number(index)

            });
            //! If Image, update image
            if (domCards[index].src !== null) {
                domCards[index].src = image;
            }
            //* Call matchCards after update, asynchronous so timeout .1ms
            setTimeout(() => {
                this._matchCards(domCards);
            }, 100);

        }

    } //!end setCards

    /*
 TODO:  MatchCards Function

    */
    _matchCards = (domCards) => {
        //*pull cardOne and CardTwo from state both contain card values
        const {cardOne, cardTwo, indexOne, indexTwo} = this.state;

        console.log("matchCards called"); //*Debug

        //* If Match, update match, 1 second to match to let user see values
        setTimeout(() => {
            if (cardOne === cardTwo) {
                this.setState({
                    ...this.state,
                    match: true,
                    selected: 0
                });
                this._updateCards(this.state.match);
            } else {
                //*Doesnt match, reset
                this.setState({
                    ...this.state,
                    selected: 0
                });
                //!Doesnt Match, want to reset the images back to cardBack
                domCards[indexOne].src = cardBack;
                domCards[indexTwo].src = cardBack;
                this._updateCards(this.state.match);
            }

        }, 600);
    }
    /*
    TODO: updateCards()
    */
    _updateCards = (match) => {
        //*Function checks if match, logic accordingly
        console.log("Update cards called") //*debug
        if (match) {
            //*match *reset Card Values - update Card array
            const {cards, indexOne, indexTwo} = this.state;

            const pile = cards.filter((card, index) => {
                return index === indexOne || index === indexTwo;
            });
            const newCards = cards.map((card, index) => {

                //*If index does not match, return card
                //* Else, return matchedCard
                return (index !== indexOne && index !== indexTwo
                    ? card
                    : {
                        ...card,
                        matched: true
                    });
            });
            //*update card array
            this.setState({
                ...this.state,
                cards: newCards,
                cardPile: this
                    .state
                    .cardPile
                    .concat(pile)
            });
            console.log("UpdateCards()");
            this._resetCardValues();
        } else {
            //*no match - reset - do nothing
            this._resetCardValues();
        }
    }
    /*
    TODO: resetCardValues()
    */
    _resetCardValues = () => {
        this.setState({
            ...this.state,
            cardOne: {},
            cardTwo: {},
            indexOne: 0,
            indexTwo: 0,
            match: false
        });
    }
    // !-- Render Method

    /*
    TODO: render()
    */
    render()
    {
        //* Pull from state and constructor
        const {deck, cards, card_value_one, card_value_two, cardPile} = this.state;
        const setCards = this._setCards;
        return (

            <div className="main">

                <Header _shuffleDeck={this._shuffleDeck} cardPile={cardPile}/> {(this.state.match && <h3 className="main__matched">Matched!</h3>)}
                {(!this.state.deck) && <h1>Loading...</h1>}
                <CardList
                    deck={deck}
                    cards={cards}
                    cardOne={card_value_one}
                    cardTwo={card_value_two}
                    _setCards={this._setCards}/>
                <Footer/>
            </div>
        )
    }
}

export default Main;


