import React, {Component} from 'react';
import propTypes from 'prop-types';
import styled from 'styled-components';

//*Import image
import cardBack from "../../assets/playing-card-back.png";

const Card = ({
    card = null,
    _setCards,
    index
}) => {

    /*
 *  Receive from props -card
 *  Access to
 *  {code:, image: images:{}, suit, value}
 ! Use Value to make comparisons
*/
    //! _Cusotmf unctions
    const _loadCardImg = () => {
        if (card && card.image) {
            return cardBack;
        }
    }
    const cardImg = card.image;
    return (

        <div className="cardlist__card" o>
            <div className="cardlist__card--img-container">
                {(card.matched) && <img className="cardlist__card--img" data-matched ={true}/>
}
                {(!card.matched && <img
                    onClick={_setCards}
                    className="cardlist__card--img"
                    src={(card.matched
                    ? null
                    : cardBack)}
                    alt={card.suit}
                    data-value={card.value}
                    data-index={index}
                    data-image={card.image}
                    data-matched={false}/>)}

            </div>

        </div>

    );
}
export default Card;
//! PropType Check

Card.propTypes = {
    card: propTypes.object,
    _setCards: propTypes.func.isRequired,
    index: propTypes.number
}


