import React, {Component} from 'react';
import propTypes from 'prop-types';

//*Import Components

const Header = ({
    cardPile = null,
    _shuffleDeck
}) => {
    return (
        <div className="header">
            <h1 className="header__head">
                React - Concentration Card Game</h1>
            <p className="header__sub">A Card Matching game built in React, take a peak under the hood
                <a
                    className="header__link"
                    href="https://github.com/WingChhun/React-ConcentrationCardgame">
                    here</a>

            </p>
            <ol className="header__instructions">

                <li className="header__instructions--item">Press New Game to shuffle the deck, restart the game</li>
                <li className="header__instructions--item">Match all pairs of cards on the board</li>
                <li className="header__instructions--item">Game ends when all cards have been matched</li>
                <li className="header__instructions--item">Note : There are 52 cards total, and Cards Matched displays amount matched</li>
            </ol>
            <div className="header__active-container">
                <h3 className="header__new" onClick={_shuffleDeck}>
                    New Game</h3>
                <h3 className="header__cardPile">
                    Cards Matched : {cardPile.length}</h3>
            </div>
        </div>
    )
}
export default Header; //! PropTypes check
Header.propTypes = {
    cardPile: propTypes.array.isRequired,
    _shuffleDeck: propTypes.func.isRequired
}



