import React from 'react';

const Footer = () => {

    return (

        <footer className="footer">

            <ul className="footer__list">
                <li className="footer__list--item"><a href="https://github.com/WingChhun" className="fa fa-2x fa-github-square"/></li>
                <li className="footer__list--item"><a
                    href="https://www.instagram.com/wingchhun/"
                    className="fa fa-2x fa-instagram"/></li>
                <li className="footer__list--item"><a
                    href="https://www.linkedin.com/in/james-chhun-16b1b5120/"
                    className="fa fa-2x fa-linkedin-square"/></li>
                <li className="footer__list--item"><a href="https://codepen.io/WingChhun/" className="fa fa-2x fa-codepen"/></li>
            </ul>
            {/*Copyright*/}
            <p className="footer__copyright">&copy; James Chhun 2018
            </p>
        </footer>

    )

}

export default Footer;


